html{height:100vh !important}body{height:100% !important;margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}body::-webkit-scrollbar{width:8px;background-color:#e5e6e8}body::-webkit-scrollbar-thumb{border-radius:25px;background-color:#afb4b8}body::-webkit-scrollbar-track{border-radius:25px}footer{width:100%;bottom:0;color:rgba(0,0,0,0.6);text-align:end;padding:8px}footer .name{color:black !important}

.App{text-align:center;height:calc(100vh - 168px)}.App-logo{height:40vmin;pointer-events:none}@media (prefers-reduced-motion: no-preference){.App-logo{-webkit-animation:App-logo-spin infinite 20s linear;animation:App-logo-spin infinite 20s linear}}.App-header{background-color:#282c34;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.App-link{color:#61dafb}@-webkit-keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}@keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}

.day-card{max-width:200px;width:100%;cursor:pointer;margin:0 8px;min-height:350px;border:1px solid rgba(0,0,0,0.2);border-radius:4px;padding:16px;box-shadow:0px 0px 4px 2px rgba(0,0,0,0.1);justify-self:center}.day-card:hover{margin-top:-16px;box-shadow:0px 0px 8px 2px rgba(111,66,193,0.6)}

.week-container{display:table;color:white;flex-flow:column;height:calc(100vh - 168px);box-sizing:border-box;height:100%;width:100%}.week-container .page-content{padding:32px;flex:1 1}.week-container .page-content .city{font-size:40px;margin:0;margin:16px 0}.week-container .page-content .content{color:rgba(0,0,0,0.87);display:flex;flex-flow:column;justify-content:center;padding:32px 0;padding-top:48px}.week-container .page-content .content .cards{display:grid;grid-template-columns:repeat(auto-fill, minmax(220px, 1fr));grid-auto-rows:350px;grid-column-gap:0.5em;-webkit-column-gap:0.5em;column-gap:0.5em;grid-column-gap:0.5em;grid-row-gap:0.5em;row-gap:0.5em;grid-row-gap:0.5em}@media (min-width: 1400px){.week-container .page-content .content .cards{grid-template-columns:repeat(auto-fill, minmax(280px, 1fr))}}

.day-container{display:flex;padding:32px;height:calc(100vh - 168px);justify-content:space-evenly;box-sizing:border-box}.day-container .page-left{display:flex;flex-flow:column;justify-content:space-around}.day-container .page-left .title .city{color:rgba(0,0,0,0.87)}.day-container .page-left .title .time{color:rgba(0,0,0,0.3)}.day-container .page-left .graph{display:block}.day-container .page-right{display:flex;flex-flow:column;justify-content:center}.day-container .page-right .temperatures{width:250px;overflow:auto;padding:0px 16px;color:rgba(0,0,0,0.87)}.day-container .page-right .temperatures .temperature{border:1px solid rgba(0,0,0,0.2);margin:16px 0;border-radius:4px;padding:4px 8px;box-shadow:0px 0px 8px 2px rgba(0,0,0,0.1)}.day-container .page-right .temperatures .temperature .time{margin:0 8px;margin-right:4px}.day-container .page-right .temperatures .temperature .degrees{margin:0 8px;margin-left:4px}.day-container .page-right .temperatures::-webkit-scrollbar{width:8px;background-color:#e5e6e8}.day-container .page-right .temperatures::-webkit-scrollbar-thumb{border-radius:25px;background-color:#afb4b8}.day-container .page-right .temperatures::-webkit-scrollbar-track{border-radius:25px}@media (max-width: 760px){.day-container{flex-flow:column;box-sizing:border-box}.day-container .page-left .graph{display:none}.day-container .page-right{margin-top:16px;align-self:center}}

.header{color:white;background:#6f42c1;height:128px;display:flex;flex-flow:column;justify-content:center;font-size:48px;margin:0 !important;cursor:pointer}a:hover,a,a:active,a:focus{color:white !important;text-decoration:none !important;outline:none !important}@media (max-width: 600px){.header{font-size:32px}}

